<template>
  <div class="love-album">
    <div class="header">
      <div class="header-left">
        <!-- 图片点击区域 -->
        <img 
          class="avatar" 
          :src="LoveAlbumData.avatar"
        >
        <h1 class="album-title">{{ LoveAlbumData.albumTitle }}</h1>
      </div>

      <div class="page-index-info">
        <button v-for="(item, index) in LoveAlbumData.headerNav" :key="index" type="button" class="page-index-btn"
          :class="{ 'active': index === flipToIndex }" @click="flipTo(item.flipToIndex)">
          {{ item.title }}
        </button>
      </div>


      <div class="page-nav-container">
        <div class="page-nav">
          <button type="button" class="btn-prev" @click="flipPrev">
            <span class="heart-icon">❤</span> Previous
          </button>
          <button type="button" class="btn-next" @click="flipNext">
            Next <span class="heart-icon">❤</span>
          </button>
        </div>

        <div class="love-time-info">
          <div class="time-title">琳可爱，我们已经相恋了</div>
          <div v-if="togetherTimeResult" id="elapseClock">
            <span class="heart-icon">❤</span>
            {{ togetherTimeResult }}
          </div>
        </div>
      </div>

    </div>

    <div class="container">
      <div class="flip-book html-book stf__parent" id="demoBookExample">
        <!-- 封面 -->
        <div class="page page-cover page-cover-top" data-density="hard">
          <div class="page-content">
            <h2>{{ LoveAlbumData.coverTitle }}</h2>
            <p class="cover-subtitle">{{ LoveAlbumData.coverSubtitle }}</p>
            <img :src="LoveAlbumData.coverStartImgUrl" class="cover-image">
          </div>
        </div>

        <!-- 动态渲染页面 -->
        <div v-for="(page, index) in LoveAlbumData.pages" :key="index" class="page"
          :class="{ '--left': index % 2 === 0, '--right': index % 2 !== 0 }">
          <div class="page-content">
            <h2 class="page-header">{{ page.title }}</h2>
            <div class="page-image" :style="{ backgroundImage: page.imgUrl ? 'url(' + page.imgUrl + ')' : 'none' }">
            </div>
            <div class="page-text">{{ page.content }}</div>
            <div v-if="page.time" class="page-time">{{ page.time }}</div>
            <div class="page-footer">{{ index + 1 }}</div>
          </div>
        </div>

        <!-- 封底 -->
        <div class="page page-cover page-cover-bottom" data-density="hard">
          <div class="page-content">
            <h2 class="cover-end-title">THE END</h2>
            <p class="cover-subtitle">{{ LoveAlbumData.endCoverSubtitle }}</p>
            <img :src="LoveAlbumData.coverEndImgUrl" class="cover-image">
          </div>
        </div>

      </div>
    </div>

    <div class="footer">
      <div class="footer-content">
        <div class="footer-nav">
          <button type="button" class="btn-prev" @click="flipPrev">
            <span class="heart-icon">❤</span> Previous
          </button>
          <div class="page-indicator">
            <!-- 进度条 -->
            <div class="progress-bar">
              <div class="progress" :style="{ width: (currentPage / (pageCount - 1)) * 100 + '%' }"></div>
            </div>
            <div class="progress-icon">❤{{ currentPage }}/{{ pageCount - 1 }}</div>
          </div>
          <button type="button" class="btn-next" @click="flipNext">
            Next <span class="heart-icon">❤</span>
          </button>
        </div>
        <div class="footer-info">
          <span class="info-label">© 2025 祝琳可爱&小满满</span>
          <span class="info-separator">|</span>
          <span class="info-label">由全宇宙的祝福驱动</span>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
// https://nodlik.github.io/StPageFlip/
// https://github.com/Nodlik/StPageFlip?tab=readme-ov-file
import { PageFlip } from 'page-flip';
import { LoveAlbumData, timeElapse } from '@/utils/LoveAlbumData';

export default {
  name: 'LoveAlbum',
  components: {
  },
  data() {
    return {
      LoveAlbumData,
      pageFlip: null, // page flip instance
      pageCount: 0, // 页面总数
      currentPage: 0, // 当前页码
      flipToIndex: 0, // 翻到指定页面的索引
      together: new Date("2024.03.17"), // 开始一起的日子
      togetherTimeResult: '', // 在一起的天数结果
      bookState: 'read', // 书本状态
      autoSize: true, // 自动调整大小以适应容器
      bookOrientation: 'landscape', // 书本方向
    }
  },
  mounted() {
    document.addEventListener('DOMContentLoaded', () => {
      // 计算在一起的天数
      this.togetherTimeResult = timeElapse(this.together);
      // 优化：将定时器句柄保存，组件销毁时清除，防止内存泄漏
      // 组件卸载时需要清除定时器，防止内存泄漏
      this._togetherTimer = setInterval(() => {
        this.togetherTimeResult = timeElapse(this.together);
      }, 1000); // 1秒刷新一次即可，减少资源消耗
      // 在beforeDestroy/unmounted钩子中清除定时器
      this.$once('hook:beforeDestroy', () => {
        clearInterval(this._togetherTimer);
      });
      this.$once('hook:unmounted', () => {
        clearInterval(this._togetherTimer);
      });

      // 初始化翻页组件
      this.pageFlip = new PageFlip(
        document.getElementById("demoBookExample"),
        {
          width: 550, // 基础页面宽度（单位：像素）
          // height: 733, // 基础页面高度（单位：像素）
          height: 900, // 基础页面高度（单位：像素）
          size: "stretch", // 页面尺寸模式："stretch"表示拉伸以适应容器
          minWidth: 315, // 最小允许宽度（单位：像素）
          maxWidth: 1000, // 最大允许宽度（单位：像素）
          minHeight: 400, // 最小允许高度（单位：像素）
          maxHeight: 1350, // 最大允许高度（单位：像素）
          maxShadowOpacity: 0.6, // 翻页阴影的最大透明度（0-1）
          showCover: true, // 是否显示封面
          mobileScrollSupport: true, // 是否在移动设备上支持内容滚动
          flippingTime: 600, // 翻页时间（单位：毫秒）
          usePortrait: true, // 是否允许书本在竖屏模式下显示
          // 移动端触摸配置
          clickEventForward: false, // 禁用点击事件转发
          swipeDistance: 50, // 增加滑动距离阈值，防止误触
          enableSound: false, // 禁用声音效果
          // 触摸手势配置
          touchRatio: 0.5, // 触摸敏感度降低
          longTap: false, // 禁用长按
          doubleTap: false // 禁用双击
        }
      );

      this.pageFlip.loadFromHTML(document.querySelectorAll(".page"));
      this.pageCount = this.pageFlip.getPageCount();
      this.bookOrientation = this.pageFlip.getOrientation();

      // 翻页事件
      this.pageFlip.on("flip", (e) => {
        const index = LoveAlbumData.headerNav.findIndex((item) => item.flipToIndex === e.data);
        if (index !== -1) {
          const findItem = LoveAlbumData.headerNav[index];
          this.flipToIndex = findItem.flipToIndex;
        } else {
         const findItem = LoveAlbumData.headerNav.find((item) => item.includesIndex?.includes(e.data));
          if (findItem) {
            this.flipToIndex = findItem.flipToIndex;
          }
        }
          
        this.currentPage = e.data;
      });

      // 书本状态变化事件
      this.pageFlip.on("changeState", (e) => {
        this.bookState = e.data;
      });

      // 书本方向变化事件
      this.pageFlip.on("changeOrientation", (e) => {
        this.bookOrientation = e.data;
      });
    });
  },
  methods: {
    // 上一页
    flipPrev() {
      this.pageFlip.flipPrev();
    },
    // 下一页
    flipNext() {
      this.pageFlip.flipNext();
    },
    // 翻到指定页
    flipTo(index) {
      this.flipToIndex = index;
      const findItem = LoveAlbumData.headerNav[index];
      this.pageFlip.flip(findItem.includesIndex[0]);
    }
  },
  beforeDestroy() {
    if (this.pageFlip) {
      this.pageFlip.destroy();
    }
  }
}
</script>

